import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import './App.scss';
import { Accordion } from "react-bootstrap";

function MentoDetail0() {

    let state = useSelector((state) => state);
    let { id } = useParams();

    function call(){
        alert('  .');
    }

    return (
        <div className="mento-detail">
            <div className="profile">
                <div className="img"><img src={state.reducer2[0].mentoList0[id].img} alt="" /></div>
                <div className="card">
                    <div className="namediv">
                        <span className="name">{state.reducer2[0].mentoList0[id].name} </span>
                        <span className="feature badge bg-secondary">{state.reducer2[0].mentoList0[id].feature}</span>
                    </div>
                    <hr />
                    <p className="ment">{state.reducer2[0].mentoList0[id].ment}</p>
                    <div className="number">
                        <span> </span>
                        <span className="num">{state.reducer2[0].mentoList0[id].num} </span>
                    </div>
                    <hr />
                    <span className="message">  </span>
                    <div className="button">
                        <button className="send btn-dark btn-lg"> </button>
                        <button onClick={call} className="call btn-danger btn-lg"> </button>
                    </div>
                </div>
            </div>
            <div className="container">
                <span className="title"> </span>
                <div className="box">
                    <div className="percent">
                        <span>      </span>
                        <div className="bar"><div className="fill">64%</div></div>
                    </div>
                    <span className="question">   ?</span>
                    <div className="reason">
                        <ul>
                            <li><input type="radio" id='reason1' name="reason" /><label htmlFor="reason1">    </label></li>
                            <li><input type="radio" id='reason2' name="reason" /><label htmlFor="reason2">  </label></li>
                            <li><input type="radio" id='reason3' name="reason" /><label htmlFor="reason3"> </label></li>
                            <li><input type="radio" id='reason4' name="reason" /><label htmlFor="reason4">  </label></li>
                            <li><input type="radio" id='reason5' name="reason" /><label htmlFor="reason5">   </label></li>
                        </ul>
                        <button onClick={call}></button>
                    </div>
                </div>
            </div>
            <div class="container2">
                <h2 class="text-center">  </h2>
                <Accordion className="faq">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>Q. ''  ?</Accordion.Header>
                        <Accordion.Body>
                            ''               .
                        </Accordion.Body>
                    </Accordion.Item>
                    <Accordion.Item eventKey="1">
                        <Accordion.Header>Q. /  ?</Accordion.Header>
                        <Accordion.Body>
                                ,    . <br />
                              48      .
                        </Accordion.Body>
                    </Accordion.Item>
                    <Accordion.Item eventKey="2">
                        <Accordion.Header>Q. ''  /   ?</Accordion.Header>
                        <Accordion.Body>
                            ''  /    .
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </div>
        </div>
    )
}

export default MentoDetail0;