import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { Link, Route, Switch } from "react-router-dom";
import './App.scss';

function GroupUpload() {

    let state = useSelector((state) => state);
    let history = useHistory();
    let dispatch = useDispatch();

    return (
        // title, sentence, writer, introduction, field, category, theNum, deadline, address, reviewWriter, reviewSentence,
        <div className="group-upload">
            {/* <h4></h4> */}
            <form action="/group-upload" method="post">
                <input type="text" name='title' placeholder='' />
                <hr />
                <textarea name="sentence" id="" cols="30" rows="2" maxLength={20} placeholder='  !'></textarea>
                <hr />
                <select name="category" id="" required>
                    <option value="" disabled selected> </option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value="/">/</option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
                <hr />
                <select name="theNum" id="" required>
                    <option value="" disabled selected>  </option>
                    <option value="1~5">1~5</option>
                    <option value="5~10">5~10</option>
                    <option value="10~15">10~15</option>
                    <option value="15 ">15 </option>
                </select>
                <hr />
                <label htmlFor="deadline"><strong> </strong></label><input name='deadline' type="date" className='deadline' id='deadline' required />
                <hr />
                <div className="btn">
                    <button type='submit'> </button>
                </div>
            </form>
        </div>
    )
}

export default GroupUpload;

